.valut_icon{
    width: 64px;
    height: 64px;
    
}

.row{
    display: flex;
    flex-direction: column;
    align-items: center;
    border-bottom: 1px solid rgb(180, 178, 178);
    margin: 5px;
    min-width: 150px;
    height: 107px;
}

.bubble{
    margin: 1% 0 0;
    height: 20px;
    border-radius: 12px;
    border: 1px solid black;
    padding: 5px;
}

.text_bubble{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    margin-left: 10px;
    margin-right: 10px;
    margin-top: 3%;
    
}

.main_text_bubble{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    margin-left: 10px;
    margin-right: 10px;
    border-bottom: 2px solid rgb(180, 178, 178);
}