.convertor_box{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.input_box{
    margin: 5% 0 0;
    height: 40px;
    font-size: 25px;
    border-radius: 12px;
    border: 1px solid black;
    padding: 5px;
}

.select_box{
    margin: 5% 0 0;
    height: 40px;
    font-size: 25px;
    padding: 5px;
   
    border-radius: 12px;
    border: 1px solid black;
}

.convert_page{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.check_area{
    font-size: larger;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
}

#myCheck{
    top: 3px;
    width: 18px;
    height: 18px;
    
}